import React from 'react';
import styled from 'styled-components';
import { Row, Col } from 'antd';

type TSectionProps = {
  flex?: boolean;
  textAlign?: 'center' | 'left' | 'right';
  alignItems?: 'center' | 'flex-start' | 'flex-end';
  fullscreen?: boolean;
  first?: boolean;
  mode?: 'dark' | 'light';
};

const StyledSection = styled.section<TSectionProps>`
  width: 100%;
  padding-top: ${props => (props.first ? 'calc(8vh + 64px)' : '8vh')};
  padding-bottom: 8vh;
  display: ${p => p.flex ? 'flex' : 'block'};
  justify-content: center;
  align-items: ${props => props.alignItems};
  text-align: ${props => props.textAlign};
  height: ${props => (props.fullscreen ? '100vh' : 'initial')};
  box-sizing: ${props => (props.fullscreen ? ' border-box' : 'initial')};
  background-color: ${p =>
    p.mode === 'light' ? '#fff' : p.theme.colors.black};
`;

type TProps = {
  textAlign?: 'center' | 'left' | 'right';
  alignItems?: 'center' | 'flex-start' | 'flex-end';
  fullscreen?: boolean;
  first?: boolean;
  title?: string;
  desc?: string[];
  mode?: 'dark' | 'light';
  flex?: boolean;
};

const Section: React.FunctionComponent<TProps> = ({
  children,
  fullscreen = false,
  textAlign = 'left',
  first = false,
  alignItems = 'center',
  mode = 'light',
  flex = false,
}) => (
  <StyledSection
    flex={flex}
    fullscreen={fullscreen}
    textAlign={textAlign}
    first={first}
    alignItems={alignItems}
    mode={mode}
  >
    {children}
  </StyledSection>
);

export default Section;
